/*--RESET--*/
* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-family: 'Nunito', Arial, sans-serif;
	font-size: 16px;
	margin: 0;
	padding: 0;
	outline: none;
	color: #4d565d;
}
li {
	list-style-type: none;
}
a {
	color: inherit;
	text-decoration: none;
	@extend .animate;
}
a:hover,a:focus,a:visited,a:active {
	color: inherit;
	text-decoration: none;
}
/* a:focus {
	color: inherit;
	text-decoration: none;
}
a:visited {
	color: inherit;
	text-decoration: none;
} */
h1, h2, h3, h4, h5 {
	font-weight: normal;
	text-transform: uppercase;
	font-weight: 300;
}
button {
	cursor: pointer;
	@extend .animate;
}
p {
	line-height: 26px;
	margin-bottom: 20px;
	text-align: justify;
	&:last-of-type {
		margin: 0;
	}
}