/*--MEDIA--*/
@media (max-width: 1550px) {
	header {
		.logo{
			padding-left: 20px;
		}
	}
}
@media (max-width: 1400px) {
	header {
		a {
			margin-right: 30px;
		}
	}
	footer {
		.send_form {
			label {
				margin-right: 10px;
			}
		}
	}
}
@media (max-width: 1250px) {
	.welcome {
		.text_wrap {
			.read_btn {
				height: 150px;
			    width: 150px;
			    font-size: 30px;
		        right: -75px;
			    bottom: -75px;
			}
			p:last-of-type {
				padding-right: 85px;
			}
		}
	}
	.news_slider {
		.slider_item {
			.content {
				-webkit-flex-wrap: wrap;
				-ms-flex-wrap: wrap;
				flex-wrap: wrap;
				.news_image {
					width: 50%;
				}
				.news_descr {
					width: 50%;
					padding-left: 20px;
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
					-webkit-flex-wrap: wrap;
					-ms-flex-wrap: wrap;
					flex-wrap: wrap;
				}
				.news_text {
					display: none;
				}
				.news_text_mob {
					display: block;
					margin-top: 30px;
					p:last-of-type {
						padding-right: 160px;
					}
				}
				.read_btn {
					bottom: -75px;
				    height: 150px;
				    width: 150px;
				    font-size: 30px;
				}
			}
		}
	}

	footer {
		.footer_content {
			height: auto;
		}
		.container {
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
			-webkit-box-pack: center;
			-webkit-justify-content: center;
			-ms-flex-pack: center;
			justify-content: center;
			padding-top: 15px;
			padding-bottom: 15px;
			.footer_menu {
				-webkit-box-ordinal-group:3;
				-webkit-order:2;
				-ms-flex-order:2;
				order: 2;
			}
			.send_form {
				-webkit-box-ordinal-group:2;
				-webkit-order:1;
				-ms-flex-order:1;
				order: 1;
				margin-bottom: 20px;
			}
		}
	}
}
@media (max-width: 1200px) {
	header {
		.sandwich {
	    	display: block;
	    }
	    .menu {
		    nav {
				background: $white;
				display: block;
				height: auto;
				left: 0;
				right: 0;
				position: absolute;
				visibility: hidden;
				opacity: 0;
				z-index: 4;
				padding-top: 15px;
				-webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
				box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
				a {
					display: block;
					text-align: center;
					margin: 0;
					padding: 20px;
					font-size: 24px;
					color: $accent;
					/* border-bottom: 1px solid $accent; */
					&.active {
						color: $font;
					}
				}
				a:before {
					content: none;
				}
				&.active {
					opacity: 1;
					visibility: visible;
				}
			}
	    }
	}
}
@media (max-width: 1000px) {
	.top {
		.top_slider {
			.slider_item {
				 .slider_text {
				 	h1 {
				 		font-size: 50px;
				 		line-height: 55px;
				 	}
				 	h2 {
				 		font-size: 30px;
				 		line-height: 35px;
				 	}
				 }
			}
		}
		.down_btn {
			left: 50%;
			-webkit-transform: translateX(-50%);
			-ms-transform: translateX(-50%);
			transform: translateX(-50%);
		    bottom: 30px;
		    width: 40px;
		    height: 40px;
		    line-height: 42px;
		    i {
		    	font-size: 20px;
		    }
		}
	}
	.news {
		.news_title_wrap {
			width: 100%;
			max-width: 100%;
			text-align: center;
			h2 {
				width: 100%;
				max-width: 100%;
			    text-align: left;
			}
		}
	}
}
@media (max-width: 800px) {
	.welcome {
		.text_wrap {
			max-width: 100%;
			.read_btn {
				right: 0;
			}
			p:last-of-type {
				padding-right: 160px;
			}
		}
	}
}
@media (max-width: 767px) {
	.top {
		.top_slider {
			.slider_item {
				padding-top: 150px;
			    padding-bottom: 150px;
			    min-height: auto;
				.slider_text {
				    margin-left: 20px;
				    margin-right: 20px;
				    max-width: -webkit-calc(100% - 40px);
				    max-width: calc(100% - 40px);
				    position: static;
				}
			}
			.owl-dots {
				bottom: 90px;
			    left: 50%;
			    -webkit-transform: translateX(-50%);
			    -ms-transform: translateX(-50%);
			    transform: translateX(-50%);
			    top: auto;
			    background: $white;
			    padding: 10px 10px 0px 10px;
			    -webkit-box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
			    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
			    .owl-dot {
			    	display: inline-block;
			    	width: 34px;
			    	margin-right: 20px;
			    	margin-bottom: 0;
			    	&:last-of-type {
			    		margin: 0;
			    	}
			    }
			}
		}
	}
	.welcome {
		.text_wrap {
			h2 {
				font-size: 50px;
			}
			h3 {
				font-size: 30px;
				margin-bottom: 20px;
			}
			.read_btn {
		        bottom: -105px;
   			    height: 100px;
   			    width: 100px;
   			    font-size: 20px;
			}
			p:last-of-type {
				padding-right: 0;
			}
		}
	}
	.news_title_wrap {
		h2 {
			font-size: 40px;
		}
	}
	.news_slider {
		.slider_item {
			padding-top: 200px;
			.content {
				.news_image {
					width: 100%;
				}
				.news_descr {
					width: 100%;
					padding: 0;
					h3 {
						font-size: 30px;
					}
				}
				.news_text_mob {
					margin: 0;
					p:last-of-type {
						padding: 0;
					}
				}
				.read_btn {
					bottom: -105px;
				    height: 100px;
				    width: 100px;
				    font-size: 20px;
				}
			}
		}
	}
	footer {
		.send_form {
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
			-webkit-box-pack: center;
			-webkit-justify-content: center;
			-ms-flex-pack: center;
			justify-content: center;
			label {
				width: 100%;
				text-align: center;
				margin-bottom: 20px;
			}
			button {
				width: 290px;
			}
		}
		.footer_contacts {
			height: auto;
		}
		.contacts {
			text-align: center;
			margin-bottom: 10px;
		}
	}
}
@media (max-width: 550px) {
	.top {
		.top_slider {
			.slider_item {
				.slider_text {
					h1 {
						font-size: 30px;
					    line-height: 35px;
					}
					h2 {
						font-size: 20px;
					    line-height: 25px;
					}
				}
			}
			.owl-dots {
				width: -webkit-calc(100% - 40px);
				width: calc(100% - 40px);
			    text-align: center;
			}
		}
	}
	.welcome {
		.text_wrap {
			h2 {
				font-size: 30px;
				font-weight: 300;
			}
			h3 {
				font-size: 20px;
				font-weight: 300;
			}
		}
	}
	.news_slider {
		.slider_item {
			.container {
				width: 100%;
				padding-left: 30px;
			    padding-right: 30px;
			}
			.content {
				.news_descr {
					h3 {
						font-size: 25px;
					}
				}
			}
		}
		.slider-nav-btn {
			background: none;
			border: none;
			text-align: center;
			width: 30px;
			height: 30px;
			i {
				color: $accent;
				margin: 0;
				line-height: 30px;
				font-size: 30px;
			}
			&:hover {
				background: none;
				/* i {
					color: $accent;
				} */
			}
		}
		.prew-btn {
			left: 0;
    	}
    	.next-btn {
    		right: 0;
    	}
	}
	footer {
		.footer_menu {
			-webkit-flex-wrap: wrap;
			-ms-flex-wrap: wrap;
			flex-wrap: wrap;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			-webkit-flex-direction: column;
			-ms-flex-direction: column;
			flex-direction: column;
			.footer_menu_item {
				margin: 0;
				padding: 0;
				border: none;
				text-align: center;
			}
		}
	}
}