@import "_reset";
@import "_variables";

/* GENERAL STYLES */
	.container {
		max-width: 1620px;
		width: 100%;
		margin: auto;
		padding-left: 20px;
		padding-right: 20px;
	}
	.animate {
		-webkit-transition: all .3s cubic-bezier(.4,0,.2,1) !important;
		-o-transition: all .3s cubic-bezier(.4,0,.2,1) !important;
		transition: all .3s cubic-bezier(.4,0,.2,1) !important;
	}
	.read_btn {
		position: absolute;
		right: -110px;
		bottom: -110px;
		height: 220px;
		width: 220px;
		color: $white !important;
		background: $accent;
		-webkit-border-radius: 50%;
		border-radius: 50%;
		font-size: 34px;
		font-weight: 100;
		text-transform: uppercase;
		text-align: center;
	    display: -webkit-box;
	    display: -webkit-flex;
	    display: -ms-flexbox;
	    display: flex;
	    -webkit-box-align: center;
	    -webkit-align-items: center;
	    -ms-flex-align: center;
	    align-items: center;
	    border: 2px solid $accent;
	    &:hover {
			background: $white;
			color: $accent !important;
	    }
	}


/* --HEADER-- */
	header {
		display: -webkit-box;
		display: -webkit-flex;
		display: -ms-flexbox;
		display: flex;
	    -webkit-box-pack: justify;
	    -webkit-justify-content: space-between;
	    -ms-flex-pack: justify;
	    justify-content: space-between;
	    .logo {
	    	padding-top: 20px;
		    padding-bottom: 20px;
		    padding-left: 170px;
	    }
	    .menu {
	    	display: -webkit-box;
	    	display: -webkit-flex;
	    	display: -ms-flexbox;
	    	display: flex;
	    }
		.sandwich {
			display: none;
			position: absolute;
		    cursor: pointer;
		    right: 0;
		    top: 10px;
		    width: 70px;
		    height: 70px;
		    padding: 20px;
		    z-index: 5;
			.piece{
				position: relative;
				width: 100%;
				height: 4px;
				background-color: $accent;
				@extend .animate;
				&.top{
					top: 20%;
				}
				&.middle{
					top: 40%;
				}
				&.bottom{
					opacity: 1;
					top: 60%;
					@extend .animate;
				}
			}
		}
		.sandwich.active{
			.piece{
				&.top{
					top: 50%;
					-webkit-transform: rotate(135deg);
					-ms-transform: rotate(135deg);
					transform: rotate(135deg);
				}
				&.middle{
					top: 36%;
					-webkit-transform: rotate(-135deg);
					-ms-transform: rotate(-135deg);
					transform: rotate(-135deg);
				}
				&.bottom{
					top: 0;
					opacity: 0;
					-webkit-transform: rotate(90deg);
					-ms-transform: rotate(90deg);
					transform: rotate(90deg);
				}
			}
		}

	    .decor_line {
	    	height: 14px;
			width: 70px;
			background: $accent;
			display: inline-block;
			margin-left: 20px;
			margin-top: 85px;
	    }
		nav {
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			@extend .animate;
			/* &:after {
				content: '';
				height: 14px;
				width: 70px;
				background: $accent;
				display: inline-block;
				margin-left: 20px;
				margin-top: 85px;
			} */
		}
		a {
			color: $accent;
			text-transform: uppercase;
			font-size: 18px;
			margin-right: 60px;
			&:before {
				content: '';
				height: 80px;
				width: 2px;
				background: $accent;
				display: block;
				@extend .animate;
			}
			&:last-child {
				margin-right: 0;
				/* &:after {
					content: '';
					height: 14px;
					width: 70px;
					background: $accent;
					display: inline-block;
					margin-left: 20px;
				} */
			}
			&:hover:before {
				background: $font;
			}
			&.active {
				color: $font;
				&:before {
					background: $font;
				}
			}
		}
	}

/* --TOP-- */
	.top {
		position: relative;
		.top_slider {
			position: relative;
			.slider_item {
				/* padding-top: 250px;
				padding-bottom: 250px; */
			    min-height: 100vh;
				.slider_text {
					position: absolute;
				    /* top: 100px; */
				    margin-left: 155px;
				    max-width: 660px;
				    /* width: 100%; */
				    height: 50%;
				    /* z-index: 1; */
				    border-left: 2px solid $accent;
				    padding-left: 10px;
				    padding-right: 10px;
				    background: rgba(255, 255, 255, 0.5);
			        top: 50%;
				    -webkit-transform: translateY(-50%);
				    -ms-transform: translateY(-50%);
				    transform: translateY(-50%);
				    h1 {
				    	font-size: 80px;
				    	line-height: 85px;
				    	margin-bottom: 20px;
				    	font-weight: 100;
				    }
				    h2 {
				    	font-size: 60px;
				    	line-height: 65px;
				    	color: $accent;
				    	font-weight: 100;
				    }
				}
			}
			.owl-dots {
				position: absolute;
			    left: 75px;
			    top: 50%;
			    -webkit-transform: translateY(-50%);
			    -ms-transform: translateY(-50%);
			    transform: translateY(-50%);
				.owl-dot {
					display: block;
					margin-bottom: 20px;
					height: 30px;
					width: 82px;
					opacity: 0.5;
					position: relative;
					border-bottom: 2px solid transparent;
					&.active {
						opacity: 1;
						border-color: $accent;
					}
					&:before {
						color: $accent;
						font-size: 24px;
						position: absolute;
						display: block;
						bottom:0;
						left: 0;
						padding-bottom: 5px;
					}
				}
				.owl-dots {
				    counter-reset: slides-num;
				}
				.owl-dot {
				    counter-increment: slides-num;
				}
				.owl-dot:before {
				    content: counter(slides-num, decimal-leading-zero)'.';
				}
			}
		}
		.down_btn {
			height: 70px;
			width: 70px;
			-webkit-border-radius: 50%;
			border-radius: 50%;
			border: 2px solid $font;
			/* background: red; */
			text-align: center;
			position: absolute;
			right: 120px;
			bottom: 100px;
			z-index: 3;
			line-height: 80px;
			cursor: pointer;
			background: rgba(255, 255, 255, 0.5);
			@extend .animate;
			i {
				font-size: 30px;
			}
			&:hover {
				background: $accent;
				border-color: $accent;
				i {
					color: $white;
				}
			}
		}
	}

