/* NEWS */
	.news {
		position: relative;
	    z-index: 1;
		/* background: url(../img/news_bg.png) center no-repeat;
		    	-webkit-background-size: cover;
		    	background-size: cover; */
    	margin-top: -100px;
    	overflow: hidden;
    	.news_title_wrap {
    		text-align: right;
    		position: absolute;
		    z-index: 2;
		    top: 100px;
		    /* right: 0; */
		    left: 50%;
		    -webkit-transform: translateX(-50%);
		    -ms-transform: translateX(-50%);
		    transform: translateX(-50%);
	        width: -webkit-calc(100% - 80px);
	        width: calc(100% - 80px);
    	}
    	h2 {
    		/* padding-top: 170px; */
		    text-align: left;
		    font-size: 60px;
		    border-bottom: 2px solid;
		    max-width: 740px;
		    width: 100%;
    		display: inline-block;
    		width: -webkit-calc(100% - 435px);
    		width: calc(100% - 435px);
    		/* margin-bottom: 40px; */
    	}
	}
	.news_slider {
		.slider_item {
			/* height: 80vh; */
			/* padding-top: 150px; */
			background: url(../img/news_bg.png) center no-repeat;
	    	-webkit-background-size: cover;
	    	-webkit-background-size: cover;
	    	background-size: cover;
			padding-top: 250px;
			padding-bottom: 150px;
			.container {
	    		text-align: left;
	    		width: -webkit-calc(100% - 80px);
	    		width: calc(100% - 80px);
	    	}
			.content {
				display: -webkit-box;
				display: -webkit-flex;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-pack: end;
				-webkit-justify-content: flex-end;
				-ms-flex-pack: end;
				justify-content: flex-end;
				/* padding-right: 30px; */
				position: relative;
				.news_image {
					display: -webkit-box;
					display: -webkit-flex;
					display: -ms-flexbox;
					display: flex;
				    -webkit-box-align: center;
				    -webkit-align-items: center;
				    -ms-flex-align: center;
				    align-items: center;
				    -webkit-box-pack: center;
				    -webkit-justify-content: center;
				    -ms-flex-pack: center;
				    justify-content: center;
				    width: 400px;
					img {
						max-width: 100%;
						max-width: 100%;
						width: auto;
					}
				}
				.news_descr {
					max-width: 780px;
					padding-left: 35px;
					width: -webkit-calc(100% - 400px);
					width: calc(100% - 400px);
					.news_date {
						color: $accent;
						text-decoration: underline;
						font-size: 22px;
						margin-bottom: 10px;
					}
					h3 {
						font-size: 40px;
						margin-bottom: 20px;
					}
					p:last-of-type {
						padding-right: 220px;
					}
				}
				.news_text_mob {
					display: none;
				}
				.read_btn {
					right: 0;
				}
			}
		}
    	.slider-nav-btn {
    		height: 100px;
    		width: 100px;
    		background: $accent;
    		position: absolute;
    		top: -webkit-calc(50% - 50px);
    		top: calc(50% - 50px);
    		-webkit-border-radius: 50%;
    		border-radius: 50%;
    		border: 1px solid $accent;
    		@extend .animate;
    		i {
    			color: $white;
    			font-size: 50px;
			    line-height: 100px;
    		}
    		&:hover {
    			background: $white;
    			i {
    				color: $accent;
    			}
    		}
    	}
    	.prew-btn {
			left: -50px;
			text-align: right;
			i {
				margin-right: 24px;
			}
    	}
    	.next-btn {
    		right: -50px;
    		text-align: left;
    		i {
    			margin-left: 24px;
    		}
    	}
	}

/* --FOOTER-- */
	footer {
		a:hover {
			color: $accent;
		}
		.container {
			height: 100%;
		    display: -webkit-box;
		    display: -webkit-flex;
		    display: -ms-flexbox;
		    display: flex;
		    -webkit-box-pack: justify;
		    -webkit-justify-content: space-between;
		    -ms-flex-pack: justify;
		    justify-content: space-between;
		    -webkit-box-align: center;
		    -webkit-align-items: center;
		    -ms-flex-align: center;
		    align-items: center;
		}
		.footer_content {
			background: $back;
			height: 250px;
			* {
				color: $lightfont;
				text-transform: uppercase;
				font-size: 14px;
			}
		}
		.footer_menu {
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
			.footer_menu_item {
				padding-right: 40px;
				margin-right: 40px;
				line-height: 30px;
				border-right: 1px solid $lightfont;
				&:last-of-type {
					margin-right: 10px;
				}
				a {
					display: block;
				}

			}
		}
		.send_form {
			display: -webkit-box;
			display: -webkit-flex;
			display: -ms-flexbox;
			display: flex;
		    -webkit-box-align: center;
		    -webkit-align-items: center;
		    -ms-flex-align: center;
		    align-items: center;
			label {
				font-size: 24px;
				color: #b5bbbf;
			    margin-right: 80px;
			}
			input {
				border: 2px solid $darkfont;
				background: $back;
				height: 60px;
				color: $white;
				padding-left: 10px;
				text-transform: none;
				width: 290px;
			}
			button {
				background: $darkfont;
				border: 2px solid $darkfont;
				font-size: 24px;
				height: 60px;
				width: 240px;
				/* margin-left: -4px; */
				&:hover {
					color: $accent;
				}
			}
		}
		.footer_contacts {
			height: 90px;
			background: $darkfont;
			.contacts {
				* {
					vertical-align: middle;
					font-size: 12px;
				}
				img {
					margin-right: 5px;
				}
			}
			.copyrights {
				font-size: 12px;
			}
		}
	}

